.Results ul {
  list-style-type: none;
}

.Results li {
  font-size: 1.5em;
  font-weight: 500;
  margin-bottom: 8px;
  padding-left: 15px;
  border-left: 10px solid #eee;
}

.Results li li {
  padding-left: 0;
  border-left: none;
  font-size: 0.8em;
  font-weight: 200;
  margin-bottom: 0px;
}

@media (max-width: 600px) {
  .Results ul {
    padding-left: 5vw;
    margin-bottom: 8vw;
  }

  .Results li li {
    font-weight: 300;
  }

  .Results li ul {
    margin-bottom: 3vw;
  }
}



